<template>
  <div>
    <div class="header">
      <img :class="['switchIcon',{'openLight':isOpenLight}]" :src="lightSwitch" @click="light">
      <label class="title hGap">{{ msg }}</label>
      <label class="version">{{ver}}</label>
    </div>
    <!-- <div v-if="showBtn">
      <h5>[宋] 方回</h5>
      <pre>
平生无嗜好，有地种花看
万玉梅成列，千金菊作团
秋宜鸿后冷，春惜燕前寒
更喜薰风好，青青笋上竿
      </pre>
    </div> -->
    <div v-if="showBtn" class="hello">
      <span>流量设置  (流速 ≈ {{speed}}毫升 / 秒)</span>
      <input type="range" v-model="total" max="1000" min="0">
      <span>{{calculationMessage}}</span>
      <label for="autoStop">
        <input id="autoStop" type="checkbox" v-model="isSmartMode">
        <span>开始智能控水</span>
      </label>
      <button class="btn" @click="addWater">
        浇水
      </button>
    </div>
    
    <div class="hello">
        <div v-if="!showBtn">
          <h3>浇水中...</h3>
          <br/>
          <CirclePercent  :percent='percent' background="#42b983"></CirclePercent>
        </div>
    </div> 
    <h3>当前土壤情况:{{status}}</h3>
    
  </div>
</template>

<script>
import CirclePercent from './CirclePercent';
import axios from 'axios';
export default {
  name: 'HelloWorld',
  components:{
    CirclePercent
  },
  data(){
    return {
      msg: '护花使者',
      ver: '( ver 1.0 )',
      isOpenLight: false,
      lightSwitch:require('@/assets/kaideng_1.png'),
      isSmartMode:true,
      speed: 17.5,
      spend: 0,
      total: 300,
      percent: 0,
      status: '未知',
      showBtn: true
    }
  },
  mounted() {
    axios.get('/api/water').then((result)=>{
      this.status = result.data.dry ? '干燥' : '湿润';
    });
    axios.get('/api/light').then( (result)=>{
      this.isOpenLight = result.data.light;
    });
    
  },
  computed: {
    calculationMessage(){
      let spend = Math.floor(this.total/this.speed);
      return `浇水${this.total}毫升,预计花费时间${spend}秒`;
    }
  },
  methods: {
    light(){
      axios.post('/api/light',{
        light: !this.isOpenLight
      }).then((result)=>{
        this.isOpenLight = result.data.light;
      })
    },
    addWater(){
      let spend = Math.floor(this.total/this.speed);
      let step = Math.floor(100/spend);
      this.showBtn = false;
      this.percent = 0;
      this.animate(step);
      
      axios.post('/api/water',{
        smartMode: this.isSmartMode,
        seconds: spend,
      }).then((result)=>{
        this.showBtn = true;
        this.status = result.data.dry ? '干燥' : '湿润';
      })
    },
    animate(step){
      let timer = setInterval(()=>{
        if(this.percent >= 100){
          clearInterval(timer);
          this.showBtn = true;
          this.percent = 0;
        }else{
          this.percent += step;
        }
      },1000);
    }
  },
}
</script>

