<template>
    <div class="circle-bar" :style="{'backgroundColor': background}">
        <div class="circle-bar-left" :style="{'transform': leftCircleRotate}"></div>
        <div class="circle-bar-right" :style="{'transform': rightCircleRotate, 'backgroundColor': rightCircleBackground, 'transitionProperty': transitionPropertyFunc}"></div>
        <!-- 遮罩层，显示百分比 -->
        <div class="mask">
            <span class="percent">{{percent}}%</span>
        </div>
    </div>
</template>
<style scoped>
.circle-bar {
    font-size: 200px;
    width: 1em;
    height: 1em;
    position: relative;
    background-color: #42b983;
}
.circle-bar-left,
.circle-bar-right {
    width: 1em;
    height: 1em;
    background-color: #eee;
    /* transition: all .3s ease; */

    transition-timing-function: ease;
    transition-duration: .3s;
}
.circle-bar-right {
    clip: rect(0, auto, auto, 0.5em);
}
.circle-bar-left {
    clip: rect(0, 0.5em, auto, 0);
}

.mask {
    width: 0.8em;
    height: 0.8em;
    background-color: #fff;
    text-align: center;
    line-height: 0.2em;
    color: #42b983;
}
.mask :first-child {
    font-size: 0.3em;
    height: 0.8em;
    line-height: 0.8em;
    display: block;
}
/*所有的后代都水平垂直居中，这样就是同心圆了*/
.circle-bar * {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    margin: auto;
}
/*自身以及子元素都是圆*/
.circle-bar,
.circle-bar > * {
    border-radius: 50%;
}
</style>
<script>
export default {
    name: "CircelPercent",
    props: {
        percent: Number,
        background: String
    },
    computed: {
        transitionPropertyFunc(){
            if(this.percent > 50){
                return 'rotate';
            }
            return 'all';
        },
        rightCircleBackground(){
            if(this.percent > 50){
                return this.background ? this.background : '#42b983';
            }
            return '#eee';
        },
        rightCircleRotate() {
            let rotate = "";
            if(this.percent <= 50) {
                rotate = "rotate(" + this.percent * 3.6 + "deg)";
            } else {
                rotate = "rotate(0deg)";
            }
            return rotate;
        },
        leftCircleRotate() {
            let rotate = "";
            if(this.percent > 50) {
                rotate = "rotate(" + (this.percent - 50) * 3.6 + "deg)";
            }
            return rotate;
        }
    },
};
</script>


